.burger {
  width: 18px;
  height: 16px;
  z-index: 2;
  justify-content: flex-end;
  align-items: center;
  margin-top: 9px;
  margin-left: auto;
  display: none;
  position: relative;
}

.burger span {
  height: 2px;
  width: 100%;
  background-color: #fcfcfb;
  transform: scale(1);
}

.burger:before, .burger:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #fcfcfb;
  transition: all .3s;
  position: absolute;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger.active span {
  transform: scale(0);
}

.burger.active:before {
  top: 50%;
  transform: rotate(-45deg)translate(0, -50%);
}

.burger.active:after {
  bottom: 50%;
  transform: rotate(45deg)translate(0, 60%);
}

@media (max-width: 767px) {
  .burger {
    display: flex;
  }

  .nav {
    height: 100%;
    width: 100%;
    z-index: 2;
    background: linear-gradient(-57.66deg, #2d2a22 5.184%, #7e6d53 110.238%);
    flex-direction: column;
    align-items: center;
    padding: 80px 110px;
    animation: burgerAnimation .4s;
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
